
import i18n from "i18next";
//import LanguageDetector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";



i18n.use(initReactI18next).init({
  // we init with resources
  resources: {
    en: {
      translations: {
        Quotes: "Quotes",
        About: "About",
        quote: "Quote",
        author: "Author",
        'Qoute of the day': "Qoute of the day",
        'About Quote Card': "About Quote Card",
        content: `Quotes can be valuable statements that inspire and ignite minds and conversations.
        Here in this page, we view random quotes displayed in a simple design and can be changed 
        through pressing for the next quote. These cards are filled by fetching data from Quotes APIs 
        and the whole page is created through using React App.`,
        coded: "Coded by ",
        JAAA: "JAAA",
        newQuote: "Another Quote",
      }
    },
    ar: {
      translations: {
        Quotes: "",
        About: " ",
        quote: "",
        author: "",
        'Qoute of the day': " ",
        'About Quote Card': "  ",
        content: " ",
        coded: "  ",
        JAAA: " ",
        newQuote: " ",
      }
    }
  },
  fallbackLng: "en",
  debug: true,


  // have a common namespace used around the full app
  ns: ["translations"],
  defaultNS: "translations",

  keySeparator: false, // we use content as keys

  interpolation: {
    escapeValue: false, // not needed for react!!
    formatSeparator: ","
  },

  react: {
    wait: true
  }
});

export default i18n;



