import React, {useState, useEffect} from "react"
import Quote from "./Quote"

function QuoteDay({quoteDay, lang, loading}){

	useEffect( () => {
		console.log("new lang " + lang)
	}, [lang])

	return (
		<div className="container-fluid">
		  <div className="row">
		    <div className="col-2"></div>
		    <div className="col-8 align-items-center my-5 d-flex flex-column w-100">
		    {loading? 
		    	<div className="spinner-border text-info justify-content-center" role="status">
					<span className="sr-only">Loading...</span>
				</div>: 

				<Quote
					key ={lang=="English"? quoteDay.ar.id : quoteDay.en.id} 
					text={lang=="English"? quoteDay.ar.quote : quoteDay.en.quote} 
					author={lang=="English"? quoteDay.ar.author : quoteDay.en.author}
				/>
			}
		    </div>
		    <div className="col-2"></div>
		  </div>
		</div>
	)
}


export default QuoteDay


