import React from "react"

function Quote(props){

	return (
		<div className= "card shadow p-4 mb-5 rounded w-100 text-center">
			<p className="quote animate__animated animate__fadeIn">
				{props.text}
			</p>
			<h4 className="author animate__animated animate__fadeIn">
				{props.author== null? "Anonymous" : props.author}
			</h4>
		</div>
	)
}

export default Quote


