import React from "react"
import {useTranslation} from 'react-i18next'

function Footer(){

	const {t, i18n} = useTranslation()
	return (
		<div className="ftr fixed-bottom">
			<footer class="page-footer font-small w-100">
			  <div class="footer-copyright text-center py-3">
			   2020 {t("coded")} <a href="https://github.com/Jumana5"> {t("J. AAA")}</a>
			  </div>

			</footer>
		</div>
	)
}

  export default Footer


