import React, {useState, useEffect, useRef} from "react"
import "./index.scss"
import "./i18n.js"
import ArabicQuotes from "./ArQuotes"
import Header from "./Header"
import Card from "./Card"
import Footer from "./Footer"
import About from "./About"
import QuoteDay from "./QuoteDay"
import NotFound from "./NotFound"
import {BrowserRouter as BR, Switch, Route} from "react-router-dom"
import {useTranslation} from 'react-i18next'

function App (){

	const [id, setID] = useState("")
	// const [quote, setQuote] = useState("")
	// const [author, setAuthor] = useState("")
	const [loading, setLoading] = useState(true)
	const [lang, setLanguage] = useState("")
	const [allQuotes, setAllQuotes] = useState([])
	const [arQuotes, setArabic] = useState(ArabicQuotes)
  	const {t, i18n} = useTranslation()
  	
  	const [quoteDay, setQuoteDay] = useState({
  		en:{
  			id: "",
  			quote: "",
  			author: ""
  		},
  		ar:{
  			id: "",
			quote: "",
  			author: ""
  		}
  	})

	useEffect(() => { 
		const url = `https://api.paperquotes.com/apiv1/quotes/?lang=${lang}`
		const url2 = 'https://favqs.com/api/qotd/'
		const url3 = 'https://favqs.com/api/quotes/'
		const apikey = process.env.REACT_APP_KEY;

		setLoading(true)
		//quote of the day fetching
		fetch(url2, 
		{
			method: 'GET',
			headers: {Authorization: `Token token="${apikey}"`}
		})
		.then(response => response.json())
		.then(data => {
			// setID(data.quote.id)
			// setQuote(data.quote.body)
			// setAuthor(data.quote.author)
			setQuoteDay(prevState => (
			{
	            ...prevState,
	            en: {
	            	id: data.quote.id,
	            	quote: data.quote.body,
	            	author: data.quote.author
	            }
        	}
        	))
			}
		)
		//a list of quotes fetching
		fetch(url3, 
		{
			method: 'GET',
			headers: {Authorization: `Token token="${apikey}"`}
		})
		.then(response => response.json())
		.then(data => {
			setAllQuotes(data.quotes)
			}
		)

		//assigning arabic quote
		const rnum = Math.floor((Math.random() * arQuotes.length))
		setQuoteDay(prevState => (
			{
	            ...prevState,
	            ar: {
	            	id: arQuotes[rnum].id,
	            	quote: arQuotes[rnum].text,
	            	author: arQuotes[rnum].author
	            }
        	}
        	))

	}, [])


	useEffect(() => {
		if (allQuotes.length != 0){
			setLoading(false)
		}
	}, [allQuotes])


	const changeLanguage = () => {
		setLanguage(lang == "English"? "": "English")
		
		if (lang == ""){
			document.getElementsByTagName('body')[0].classList.add("ar")
			i18n.changeLanguage("ar")
			
		} else {
			document.getElementsByTagName('body')[0].classList.remove("ar")
			i18n.changeLanguage("en")
		}
     	//another way by unique id for the body
	}


	return (
		<BR>
				<Header lang={lang} changeLanguage={changeLanguage}/>
				<Switch>
					<Route exact path='/'>
						<QuoteDay quoteDay={quoteDay} lang={lang} loading={loading}/>
					</Route>
					<Route path='/about' or path='/quote_card_react_project/' component={About}/>
					<Route path="/quotes">
						<Card loading={loading} 
						allQuotes={allQuotes}
						arQuotes={arQuotes}
						lang={lang}/>
					</Route>
					<Route path="*" element={NotFound} />
					<Route>

					</Route>
				</Switch>
				<Footer/>
		</BR>
	)
}


export default App


