import React, {useState, useEffect} from "react"
import Quote from "./Quote"
import {useTranslation} from 'react-i18next' 


function Card(props){

	const [quote, setQuote] = useState({})
	const {t, i18n} = useTranslation()

	function changeQuote(){
		if (props.lang == "English"){
			const rnum = Math.floor((Math.random() * props.arQuotes.length))
			setQuote(props.arQuotes[rnum])
		} else {
			const rnum = Math.floor((Math.random() * props.allQuotes.length))
			setQuote(props.allQuotes[rnum])
		}
	}


	useEffect(() => {
		if (props.allQuotes.length != 0){
			setQuote(props.lang == "English"? props.arQuotes[0]:props.allQuotes[0])
		}
	}, [props.allQuotes])

	useEffect(() => {
		console.log(quote)
		changeQuote()
	}, [props.lang])

	return (
		<div className="container back-ground">
		  <div className="row ">
		    <div className="col-2 m-4"></div>
		    <div className="col-8 align-items-center my-5 d-flex flex-column w-100">
		      {
		      	props.loading  || !quote? 
		      	<div className="spinner-border text-info" role="status">
					<span className="sr-only">Loading...</span>
				</div>: 
					<Quote
						key ={quote.id} 
						text= {props.lang == "English"? quote.text:quote.body}
						author={quote.author} 
					/>
			}
				<button className="btn btn-secondary " onClick={changeQuote}>
					{t("newQuote")}  
				</button>
		    </div>
		    <div className="col-2"></div>
		  </div>
		</div>
	)
}


export default Card


